.figmacenter-overall-container{
    background-color: #F0F0F0;
    width: 64%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-bottom: 100px;

}

.figmacenter-main-h{
    background: linear-gradient(180deg, #1A1A1A 15.12%, rgba(26, 26, 26, 0.00) 138.37%);
    background-clip: text;
    text-decoration: underline;
    text-decoration-color: #3F8AE2;
    text-decoration-thickness:1px;
    margin: 20px 0 0 0;
    padding: 0%;
    text-align: center;
    font-size: 45px;
    font-style: normal;
    font-weight: 900;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.figmacenter-image{
    height: 200px;
    transition: filter 0.3s ease;
    filter: grayscale(100%);
}

.figmacenter-image:hover {
    filter: none; 
}

.figmacenter-first-group{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.fic{
    position: relative;
}

.fdd{
    position:absolute;
    right:-20px;
    bottom: -10px;
}

.figmacenter-text{
    padding: 5px;
    width: fit-content;
    color: var(--PortWhite, #FFF);
    font-family: "Red Hat Display";
    font-size: 12.993px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-left: 13px;
    margin-top: -7px;
}

.figmacenter-second-group{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.div-ur{
    display: flex;
    width: 73.03px;
    height: 73.03px;
    transform: rotate(-5.75deg);
    padding: 8.394px;
    justify-content: center;
    align-items: center;
    background: #FBF49A;
    box-shadow: 0px 3.358px 3.358px 0px rgba(0, 0, 0, 0.25);
    gap: 8.394px;
    flex-shrink: 0;
    color: #000;
    text-align: center;
    font-family: "Nanum Pen";
    font-size: 9.11px;
    font-style: normal;
    font-weight: 400;
    line-height: 15.949px; /* 105.556% */
    text-transform: uppercase;
}

.div-ds{
    display: flex;
    width: 73.03px;
    height: 73.03px;
    transform: rotate(11.642deg);
    padding: 8.394px;
    align-items: flex-start;
    gap: 8.394px;
    flex-shrink: 0;
    color: #000;
    background: #FFD19B;
    box-shadow: 0px 3.358px 3.358px 0px rgba(0, 0, 0, 0.25);
    text-align: center;
    font-family: 'Nanum Pen Script';
    font-size: 9.162px;
    font-style: normal;
    font-weight: 400;
    line-height: 13.162px; /* 100% */
    text-transform: uppercase;
}

.div-dt{
    display: flex;
    width: 73.03px;
    height: 73.03px;
    transform: rotate(-7.75deg);
    padding: 8.394px;
    justify-content: center;
    align-items: center;
    gap: 8.394px;
    flex-shrink: 0;
    color: #000;
    background: #E0A6F4;
    box-shadow: 0px 3.358px 3.358px 0px rgba(0, 0, 0, 0.25);
    text-align: center;
    font-family: "Nanum Pen Script", cursive;
    font-size: 9.467px;
    font-style: normal;
    font-weight: 400;
    line-height: 12.628px; /* 95.455% */
    text-transform: uppercase;
}

.figmacenter-desc-div-3{
    right:-60px;
    bottom: -20px;
}
.figmacenter-image1desc1{

}

@media (max-width:1200px) {
    .figmacenter-first-group{
        margin-bottom: 50px;
    }

    .figmacenter-overall-container{
        width: 100%;
        padding: 0 7% 100px 7%;
    }

    .figmacenter-main-h{
        margin-bottom: 50px;
    }
}

@media (max-width:900px) {
    .figmacenter-overall-container{
        width: 100%;
        padding: 0 10% 100px 10%;
    }

    .figmacenter-main-h{
        font-size: 35px;
        margin-bottom: 30px;
    }

    .figmacenter-image{
        height: auto;
        width: 150px;
    }

    .div-dt, .div-ur, .div-ds{
        width: 55px;
        height: 55px;
    }

    .svg-wrtings{
        height: 25px;
    }
}

@media (max-width:600px) {
    .figmacenter-overall-container{
        width: 100%;
        padding: 0 5% 100px 5%;
    }

    .figmacenter-main-h{
        margin-bottom: 30px;
    }

    .figmacenter-image{
        height: auto;
        width: 120px;
    }

    .div-dt, .div-ur, .div-ds{
        display: none;
    }
}

@media (max-width:500px) {
    .figmacenter-overall-container{
        width: 100%;
        padding: 0 5% 100px 5%;
    }

    .figmacenter-main-h{
        font-size: 30px;
        margin-bottom: 30px;
    }

    .figmacenter-first-group{
        flex-direction: column;
    }

    .figmacenter-first-group{
        margin-bottom: 0px;
    }

    .figmacenter-second-group{
        flex-direction: column;
    }

    .fic{
        margin-bottom: 30px;
    }
    .figmacenter-image{
        height: auto;
        width: 250px;
    }

    .div-dt, .div-ur, .div-ds{
        display: none;
    }

    .fdd, .figmacenter-desc-div-0, .figmacenter-desc-div-1,.figmacenter-desc-div-2, .figmacenter-desc-div-4{
        position:absolute;
        right:-20px;
        bottom: -10px;
    }
    .figmacenter-desc-div-3{
        right:-50px;
        bottom: -10px;
    }

    .figmacenter-desc-div-4{
        right:-5px;
        bottom: -10px;
    }

}

